:root {
  --very-dark-grayish-blue: #48556a;
  --desaturated-dark-blue: #6d7f97;
  --grayish-blue: #9eafc2;
  --light-grayish-blue: #ecf2f8;
  --white: #fff;
}

body {
  background-color: var(--light-grayish-blue);
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Manrope, sans-serif;
  display: flex;
}

@media (width >= 375px) and (width <= 1023px) {
  .card_container {
    background-color: var(--white);
    border-radius: 15px;
    width: 90vw;
    height: 128vw;
    margin: 0;
    padding: 0;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .main_card {
    flex-direction: column;
    display: flex;
  }

  .card_image {
    object-fit: cover;
    object-position: center;
    clip-path: inset(0 0 18%);
    filter: drop-shadow(0 4px 6px #ffffffe6) brightness(1.1) contrast(.9);
    border-radius: 1rem 1rem 0 0;
    width: 100%;
    height: 50%;
    transform: translateY(-1rem);
  }

  .card_content {
    background-color: hsl(210, 46, 95);
    box-shadow: none;
    border-radius: .5rem;
    padding: 0 9.5vw;
    transform: translateY(-7vw);
  }

  .card_title {
    color: var(--very-dark-grayish-blue);
    font-size: 4.5vw;
    font-weight: 700;
  }

  .card_description {
    color: var(--grayish-blue);
    margin-top: 4vw;
    font-size: 3.6vw;
    font-weight: 400;
  }

  .card_footer {
    align-items: center;
    margin-top: 9vw;
    display: flex;
  }

  .card_author {
    align-items: center;
    margin-right: 4.5vw;
    display: flex;
  }

  .author_image {
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
    width: 11vw;
    height: auto;
  }

  .author_info {
    flex-direction: column;
    justify-content: center;
    align-items: start;
    width: 100%;
    display: flex;
  }

  .author_name {
    color: var(--very-dark-grayish-blue);
    font-size: 3.6vw;
    font-weight: 700;
  }

  .author_date {
    color: var(--grayish-blue);
    margin-top: 1vw;
    font-size: 3.6vw;
    font-weight: 400;
  }

  #share_button {
    background-color: var(--light-grayish-blue);
    cursor: pointer;
    border-style: none;
    border-radius: 50%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 9.5vw;
    height: 9.5vw;
    display: flex;
  }

  .share_icon {
    fill: var(--very-dark-grayish-blue);
    width: 4.5vw;
    height: auto;
  }

  .floating_share {
    background-color: var(--very-dark-grayish-blue);
    z-index: 1;
    width: 100%;
    height: auto;
    display: none;
    position: absolute;
    transform: translateY(110vw);
  }

  .floating_container {
    justify-content: start;
    align-items: center;
    padding: 4.1vw 9.5vw;
    display: flex;
  }

  .mobile_share_name {
    text-transform: uppercase;
    color: var(--grayish-blue);
    letter-spacing: 1vw;
    margin-right: 3.5vw;
    font-size: 3.6vw;
  }

  .mobile_share_logo {
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-right: 15vw;
    display: flex;
  }

  .icon_list {
    justify-content: space-between;
    align-items: center;
    gap: 3vw;
    margin: 0;
    padding: 0;
    display: flex;
    list-style-type: none !important;
  }

  .mobile_icon {
    width: 100%;
    margin: 0 1vw;
    display: flex;
  }

  #share_invert_button {
    background-color: var(--desaturated-dark-blue);
    cursor: pointer;
    border-style: none;
    border-radius: 50%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 9.5vw;
    height: 9.5vw;
    display: flex;
  }

  .share_invert_icon {
    filter: invert() brightness(100) saturate() hue-rotate();
    width: 4.5vw;
    height: auto;
  }

  .triangle-container, .triangle-container:after, .share_invert_outer_container, #share_invert_outer_button, .share_invert_outer_icon {
    display: none;
  }
}

